
.base {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 100;
  display: block;
}
.load-once {
  background: #499eff url(../images/ji.png) no-repeat 45% 45%;
  background-size: 120px;
  opacity: 1;
  transition: all 0.5s;
}
.load-once.remove {
  opacity: 0;
}
.load-once .text {
  width: 100%;
  height: 100%;
  color: #fff;
  text-align: center;
  font-size: 20px;
  font-family: 'Microsoft YaHei', arial, sans-serif, 'Droid Sans Fallback';
  padding-top: 102%;
  display: block;
  box-sizing: border-box;
}
